    // background-position: center;
    // background-repeat: no-repeat;
    // background-size: cover;
    // padding-top: 380px;
    // padding-bottom: 300px;    
   padding-top: 178px;
   padding-bottom: 168px;
  //  background: linear-gradient(261.73deg, rgba(95, 95, 95, 0.2) -8%, rgba(185, 185, 185, 0) 107.37%), linear-gradient(98deg, rgba(0, 0, 0, 0.4) -3.49%, rgba(143, 123, 123, 0) 25.58%, rgba(255, 255, 255, 0) 98.47%), #292929;
    text-align: center;
