    background-color: var( --hero-background-color);
  // position: relative;
 }
 
 .hero__social-left{
   position: absolute;
   bottom: 0;
   left: 60px;
width: 100%;
   transform-origin: left top;
   transform: rotate(-90deg) translateX(-18px);
 border-bottom: solid 1px rgba(255, 255, 255, 0.5);
//  padding-left: 15px;
//  padding-right: 15px;
display: flex;
justify-content: flex-start;
 }
 .hero__social-list{
   display: flex;
   align-items: center;
   justify-content: center;
  //  padding: 15px;
 }
 .hero__social-item{
   &:not(:last-child){
     margin-right: 20px;
   }
 }
 .hero__social-link{
   color: inherit;
   font-style: normal;
 }
 .hero__iam{
  font-size: 10px;
  // padding-left: 120px;
  text-align: left;
  // padding-right: 120px;
  letter-spacing: 5px;
  color: var(--color-text);
}

.hero__title{
  font-family:'Trade Winds', cursive;
  // letter-spacing: 25px;
  font-weight: 500;
  font-size: 50px;
  line-height: 100px;
  color: var(--color-title);
  margin-bottom: 50px;
  margin-top: 40px;
  // padding-left: 40px;
  text-shadow: 0px 0px 50px rgb(29, 29, 29);
  
 }
 .hero__description{
  font-size: 18px;
  line-height: 1.6;
  color: var(--color-text);
padding-left: 80px;
text-align: right;
// padding-right: 80px;
// letter-spacing: 5px;
// color: rgba(170, 169, 169, 0.5);
 }
 .hero__text-box{
     max-width: 280px;
     margin: 0 auto;
   }


