 @media screen and (min-width: 768px){
  .hero{
    padding-top: 150px;
    padding-bottom: 120px;
   
  }
  .hero__iam{
    font-size: 18px;
    padding-left: 40px;
    text-align: left;
    // padding-right: 120px;
    letter-spacing: 5px;
    color: var(--color-text);
  }
 
  .hero__title{
    font-family:'Trade Winds', cursive;
    // letter-spacing: 25px;
    font-size: 130px;
    margin-bottom: 50px;
    margin-top: 40px;
    // padding-left: 40px;
    color: var(--color-title);
    text-shadow: 0px 0px 50px rgb(29, 29, 29);
  
   }
   .hero__description{
  font-size: 18px;
  padding-left: 80px;
  text-align: right;
  // padding-right: 80px;
  letter-spacing: 5px;
  color: var(--color-text);
   }
   .hero__text-box{
       max-width: 700px;
       margin: 0 auto;
     }
 }

 @media screen and (min-width: 1024px){
  .hero{
    padding-top: 200px;
    padding-bottom: 120px;
   
  }
  .hero__iam{
    font-size: 18px;
    padding-left: 40px;
    text-align: left;
    padding-right: 120px;
    letter-spacing: 5px;
    color: var(--color-text);
  }
 
  .hero__title{
    font-family:'Trade Winds', cursive;
    letter-spacing: 6px;
    font-size: 130px;
    margin-bottom: 50px;
    margin-top: 50px;
    // padding-left: 20px;
    text-shadow: 0px 0px 50px rgb(29, 29, 29);
    // max-width: 600px;
   }
   .hero__position{
     display: flex;
     align-items: center;
   }
   .hero__description{
  font-size: 18px;
  text-align: right;
  padding-right: 20px;
  letter-spacing: 5px;
  color: var(--color-text);
   }
   .hero__text-box{
       max-width: 1000px;
       margin: 0 auto;
     }
 }